(function () {

  'use strict';
    
  MyOD.module('Base', function (Base, App, Backbone, Marionette, $, _) {

    Base.SearchView = Marionette.ItemView.extend({ 

      onRender: function(){
        this.initTypeahead();
      },

      initTypeahead: function () {
        var self = this;

        var opts = {
          highlight: true,
          minLength: 3,
          hint: false
        };

        var bloodhound = App.getBloodhound();
        bloodhound.initialize();

        var datasets = {
          name: 'datasets',
          displayKey: function(item) {
            return item;
          },
          templates: {
            empty: ''
          },
          source: bloodhound.ttAdapter()
        };

        this.ui.search.typeahead(opts, datasets);
      },

      onTypeaheadSelected: function (e) {
        //clicking a selection with the mouse should initiate a search  
        this.model.set({
          q: this.ui.search.typeahead('val'),
          page: 1
        });

        this.search();
      },

      updateModel: function () {
        var q = this.ui.search.typeahead('val');
        this.model.set({
          q: q,
          page: 1
        });
      },

      onKeyDown:function(e){
        if (e.which === 13) {
          e.preventDefault();

          this.ui.search.typeahead('close');

          this.updateModel();

          this.search();
        }
      },

      onKeyUp: function () {
        this.updateModel();
      },

      onSearchButtonClick: function () {
        this.updateModel();
        this.search();
      },

      search: function () {
        App.search();
      }

    });

  });

})();


(function () {

  'use strict';
    
  MyOD.module('Models', function (Models, App, Backbone, Marionette, $, _) {
          
    Models.SearchModel = Backbone.Model.extend({ 

      defaults: {
        q: '',
        page: 1,
        per_page: 20,
        total_count: 0,
        sort_by: 'relevance'
      },

      queryStringParams: [ 'q', 'page', 'per_page', 'sort_by' ],

      getQueryString: function () {
        //create an object containing only the attributes we need to generate the querystring
        var obj = _.pick(this.toJSON(), this.queryStringParams);
        //generate querystring parameters from the object
        return $.param(obj);
      },

      getRoute: function (api) {
        // we use this function to generate routes within the app
        // AND to generate api urls - hence the api parameter
        
        var route = 'datasets';
        route += (api) ? '.json?' : '?';
        route += this.getQueryString();
        return route;
      },

      getUrl: function () {
        return App.config.api + this.getRoute(true);
      }

    });

  });

})();

