(function () {

  'use strict';
    
  MyOD.module('Models', function (Models, App, Backbone, Marionette, $, _) {
          
    Models.RowCollection = Backbone.Collection.extend({

      initialize: function (attributes, options) {
        this.dataset = options.dataset;
        
        var queryCapabilities = this.dataset.get('advanced_query_capabilities');
        this.supportsPagination = queryCapabilities && queryCapabilities.supports_pagination;

        this.orderBy = this.dataset.get('object_id_field');
      },

      perPage: 10,

      page: 0,

      orderBy: '',

      orderByAsc: true,

      model: Models.DatasetRow,

      getQueryUrl: function () {
        var url = this.dataset.get('url');
        url += '/query?where=1%3D1&text=&objectIds=&time=&geometry=&geometryType=esriGeometryEnvelope&inSR=&spatialRel=esriSpatialRelIntersects&relationParam=&outFields=*&returnGeometry=false&maxAllowableOffset=&geometryPrecision=&outSR=&returnIdsOnly=false&returnCountOnly=false&groupByFieldsForStatistics=&outStatistics=&returnZ=false&returnM=false&gdbVersion=&returnDistinctValues=false&f=pjson';

        if (this.supportsPagination) {
          url += '&resultOffset=' + this.page * this.perPage;
          url += '&resultRecordCount=' + this.perPage;
          //NOTE: when you pass in one of the above two parameters and orderByFields is left empty, 
          //map service uses the object-id field to sort the result. 
          //For a query layer with a pseudo column as the object-id field (e.g., FID), 
          //you must provide orderByFields; otherwise the query fails
        }

        var orderBy = this.orderBy;
        if (!this.orderByAsc) {
          orderBy += ' desc';
        }
        //NOTE: this still could fail 
        //if the oid field has changed since it was harvested by open data
        //or it is null (which should not happen...)
        url += '&orderByFields=' + orderBy;

        return url;
      },

      url: function () {
        return this.getQueryUrl();
      },

      parse: function (resp) {
        // this.fields = resp.fields;
        // this.fieldAliases = resp.fieldAliases;
        var rows = resp.features;
        if (!this.supportsPagination) {
          //this is slightly crappy but 
          //for datasets that don't support pagination
          //we just show the first n rows with no pagination
          //even though we got maxRecordCount rows
          rows = _.first(rows, this.perPage);
        }
        return rows;
      }

    });

  });

})();


(function () {

  'use strict';
    
  MyOD.module('DatasetsModule', function (DatasetsModule, App, Backbone, Marionette, $, _) {
         
    DatasetsModule.TableRowView = Marionette.ItemView.extend({ 

      initialize: function (options) {
        this.dataset = options.dataset;
        this.fields = this.dataset.get('fields');
      },

      template: JST['datasets/templates/table-row'],

      templateHelpers: function () {
        var self = this;
        return {
          fields: self.fields
        };
      },

      model: MyOD.Models.DatasetRow,

      tagName: 'tr'

    });

    DatasetsModule.TableView = Marionette.CompositeView.extend({

      initialize: function (options) {
        this.collection = new App.Models.RowCollection([], { dataset: this.model });
        this.collection.fetch();
      },

      events: {
        'click ul.pagination li a': 'onPageClicked',
        'click thead th': 'sort'
      },

      collectionEvents: {
        //we need the pagination to re-render
        'reset': 'render'
      },

      template: JST['datasets/templates/table'],

      childView: DatasetsModule.TableRowView,

      childViewOptions: function () {
        var dataset = this.model;
        return {
          dataset: dataset
        };
      },

      templateHelpers: function () {

        //defaults - this is what will be rendered if the dataset does not support pagination
        var obj = {
          firstPage: '',
          lastPage: '',
          prevPage: 0,
          nextPage: 0,
          pages: [],
          showPagination: false,
          from: 1,
          to: this.collection.perPage,
          total: this.model.get('record_count'),
          sortField: this.collection.orderBy,
          sortClass: this.collection.orderByAsc ? 'sort_asc' : 'sort_desc',
          sortIconClass: this.collection.orderByAsc ? 'glyphicon-arrow-down' : 'glyphicon-arrow-up',
        };

        if (this.collection.supportsPagination) {
          var totalPages = Math.ceil(this.model.get('record_count') / this.collection.perPage);
          //zero based page index
          var page = this.collection.page;

          //don't show more than 10 pages in paginator?
          var start = (totalPages > 10 && page > 6) ? page - 5 : 1;
          var end = (totalPages > start + 9) ? start + 9 : totalPages;

          var active, pages = [];
          for (var i = start; i <= end; i++) {
            active = (i === page + 1) ? 'active' : '';
            pages.push({ page: i, active: active });
          }

          var total = this.model.get('record_count');
          var from = page * this.collection.perPage + 1;
          var to = page * this.collection.perPage + this.collection.perPage;
          to = (to <= total) ? to : total;

          obj = {
            firstPage: (page === 0) ? 'disabled' : '',
            lastPage: (totalPages === page + 1) ? 'disabled' : '',
            prevPage: page,
            nextPage: page + 2,
            pages: pages,
            showPagination: true,
            from: from,
            to: to,
            total: total,
            sortField: this.collection.orderBy,
            sortClass: this.collection.orderByAsc ? 'sort_asc' : 'sort_desc',
            sortIconClass: this.collection.orderByAsc ? 'glyphicon-chevron-down' : 'glyphicon-chevron-up',
          };
        }

        return obj;
      },

      childViewContainer: 'tbody',

      onPageClicked: function (e) {
        var anchor = $(e.target).closest('a');
        var li = anchor.closest('li');
        if (!li.hasClass('disabled') && !li.hasClass('active')) {
          var page = anchor.data('page') - 1;
          this.collection.page = page;
          this.collection.fetch({ reset: true });
        }
      },

      sort: function (e) {
        var orderBy = $(e.target).data('fieldName');
        if (orderBy) {
          if (orderBy === this.collection.orderBy) {
            this.collection.orderByAsc = !this.collection.orderByAsc;
          } else {
            this.collection.orderByAsc = true;
          }
          this.collection.page = 0;
          this.collection.orderBy = orderBy;
          this.collection.fetch({ reset: true });
        }
      }
      
    });

  });

})();


(function () {

  'use strict';
    
  MyOD.module('DatasetsModule', function (DatasetsModule, App, Backbone, Marionette, $, _) {
          
    DatasetsModule.View = Marionette.ItemView.extend({

      initialize: function (options) {
        _.bindAll(this, 'changeAttribute', 'changeRamp');
        this.mapManager = options.mapManager;
      },

      template: JST['datasets/templates/dataset'],

      id: 'page-dataset',

      ui: {
        'mapDiv': '#map',
        'tableContainer': '#table-container'
      },

      modelEvents: {
        'change': 'render'
      },

      templateHelpers: function () {
        var baseUrl = this.model.url().replace(/.json$/, '');
        return {
          baseUrl: baseUrl 
        };
      },

      onRender: function () {
        //TODO: refactor to use regions...
        this.tableView = new DatasetsModule.TableView({ el: this.ui.tableContainer, model: this.model }).render();
      },

      // onShow: function () {
      //   console.debug('onShow');
      // },

      onDomRefresh: function () {
        if (this.model.get('item_type').toLowerCase() !== 'table') {
          var self = this;
          // TODO: would be nice to encapsulate this in mapmanager so consumers wouldn't have to know to do it
          this.ui.mapDiv.show();
          this.mapManager.dojoReady.done(function () {
            self.mapManager.createMap('map', { coords: self.model.get('extent').coordinates });
            self.mapManager.addDataset(self.model);

            //self.initSmaps();
          });
        }
      },

      initSmaps: function (basemap) {
        var numerics = this.model.getNumericFields();

        if (numerics.length){

          // select the first attr to map 
          // we could put some logic around which to use here
          this.fieldName = this.fieldName || numerics[0].name;
          
          //get stats for selected attribute
          var fields = this.model.get('fields');
          this.stats = this.getStats(fields, this.fieldName);
          this.type = this.getGeometryType(this.model.get('geometry_type'));


          var yukiOptions = {
            fields: numerics,
            field: this.fieldName,
            statistics: this.stats,
            type: this.type,
            schemes: this.getSchemes()
          };

          if (!this.yuki) {
            this.yuki = new Yuki('smaps', yukiOptions);
          } else {
            $('#yuki-viz-tools').remove();
            this.yuki = new Yuki('smaps', yukiOptions);
          }

          // EVENT Handlers from yuki
          this.yuki.on('change', this.changeAttribute);
          this.yuki.on('ramp-change', this.changeRamp);

          this._execStyleMap();
        }

      },

      getStats: function (fields, fieldName) {
        var fieldObj = _.findWhere(fields, { name: fieldName });
        return fieldObj ? fieldObj.statistics : null;
      },

      getGeometryType: function (geomType) {
        switch (geomType) {
          case 'esriGeometryPoint':
          case 'esriGeometryMultipoint':
            return 'point';
          case 'esriGeometryPolyline':
            return 'line';
          case 'esriGeometryPolygon':
            return 'polygon';
          default:
            return geomType.toLowerCase();
        }
      },

      getSchemes: function (){
        var theme, styleType, self = this;
        switch (this.type){
          case 'point':
            theme = 'default';
            styleType = 'size';
            break;
          case 'polygon':
            theme = 'high-to-low';
            styleType = 'choropleth';
            break;
        }

        //TODO: move this into mapmanager
        return esri.styles[styleType].getSchemes({
          theme: theme, 
          basemap: 'dark-gray', 
          geometryType: self.type
        });
      },

      // handler for attr change 
      changeAttribute: function (fieldName) {
        //update stats for selected attribute
        var fields = this.model.get('fields');
        this.stats = this.getStats(fields, fieldName);
        this.fieldName = fieldName;

        this._execStyleMap(this.selectedScheme);
      },

      // handler for ramp UI change
      changeRamp: function (index) {
        this.selectedScheme = this.getSchemes().secondarySchemes[index];
        this._execStyleMap(this.selectedScheme);
      },

      //update the map in mapmanager
      _execStyleMap: function (scheme) {
        var self = this;

        var opts = {
          field: this.fieldName,
          basemap: 'dark-gray',
          statistics: this.stats,
          type: this.type
        };
      
        if (scheme){
          opts.scheme = scheme;
        }
        
        var foo = App.reqres.request('smaps:update:style', opts)
          .done(function(renderer){
            if (self.type === 'point'){
              self.yuki.buildPointLegend(renderer.breaks);
            }
          });
      },

      onDestroy: function () {
        this.mapManager.destroy();
        this.tableView.destroy();
      }
      
    });

  });

})();


(function () {

  'use strict';
    
  MyOD.module('DatasetsModule', function (DatasetsModule, App, Backbone, Marionette, $, _) {

    DatasetsModule.Controller = Marionette.Controller.extend({ 

      initialize: function () {
        _.bindAll(this, 'onModelFetched');
      },

      initUi: function (options) {
        this.mapManager = new App.Utils.MapManager();
        this.model = new App.Models.DatasetModel({ id: options });
        this.model
          .fetch()
            .done(this.onModelFetched)
            .fail(function () {
              App.navigate404();
            });
      },

      onModelFetched: function () {
        var view = new DatasetsModule.View({ model: this.model, mapManager: this.mapManager });
        App.layout.getRegion('main').show(view);
      },

      onBeforeDestroy: function () {
        if (this.mapManager) {
          this.mapManager.destroy();
        }
      }

    });

  });

})();


(function () {
    
  'use strict';

  MyOD.module('DatasetsModule', function (DatasetsModule, App, Backbone, Marionette, $, _) {
      
    //Router for the module
    DatasetsModule.Router = Backbone.Marionette.AppRouter.extend({
      appRoutes:{
        'datasets/:id(/)': 'show',
      }
    });

    //Add the router during the initialization 
    DatasetsModule.addInitializer(function (options) {
      var router = new DatasetsModule.Router({ controller: DatasetsModule.API }); 
    });

    //Simple API object that provides the implementation for the routes
    DatasetsModule.API = {

      show: function(options){

        if(!this.datasetsController){
          this.datasetsController = new DatasetsModule.Controller(options);
        }

        this.datasetsController.initUi(options);
      }
    };

  });
})();

(function () {

  'use strict';
    
  MyOD.module('ErrorModule', function (ErrorModule, App, Backbone, Marionette, $, _) {
          
    ErrorModule.View = Marionette.ItemView.extend({
      
      id: 'page-error',

      getTemplate: function(){
        var errorCode = this.model.get('errorCode');
        var template;

        switch (errorCode) {
          case 404:
              template = JST['error/templates/404'];
            break;
          default:
              template = JST['error/templates/500'];
        }

        return template;
      }

    });

  });

})();


(function () {

  'use strict';
    
  MyOD.module('ErrorModule', function (ErrorModule, App, Backbone, Marionette, $, _) {

    ErrorModule.Controller = Marionette.Controller.extend({ 

      initUi: function (options) {
        // init the results view in here so it re-binds on "back button"
        var model = new Backbone.Model({ errorCode: options.errorCode });
        var view = new ErrorModule.View({ model: model });

        App.layout.getRegion('main').show(view);
      }

    });

  });

})();


(function () {
    
  'use strict';

  MyOD.module('ErrorModule', function (ErrorModule, App, Backbone, Marionette, $, _) {
      
    //Router for the module
    ErrorModule.Router = Backbone.Marionette.AppRouter.extend({
      appRoutes:{
        '404': 'error404',
        '500': 'error500'
      }
    });

    //Add the router during the initialization 
    ErrorModule.addInitializer(function (options) {
      var router = new ErrorModule.Router({ controller: ErrorModule.API }); 
    });

    //Simple API object that provides the implementation for the routes
    ErrorModule.API = {

      initController: function () {
        if(!this.errorController){
          this.errorController = new ErrorModule.Controller();
        }
      },

      error404: function () {
        this.initController();
        this.errorController.initUi({ errorCode: 404 });
      },

      error500: function () {
        this.initController();
        this.errorController.initUi({ errorCode: 500 });
      }
    };

  });
})();

